import { Component, OnInit, HostListener } from '@angular/core';
import data from '../../../assets/data/DataJsonJlptVietNam.json';

@Component({
  selector: 'app-japanse-grammar',
  templateUrl: './japanse-grammar.component.html',
  styleUrls: ['./japanse-grammar.component.css']
})
export class JapanseGrammarComponent implements OnInit {

  constructor() {
  }

  levelList = ['N5', 'N4', 'N3', 'N2', 'N1', 'All'];
  selectedLevel = 'All';
  filter = true;
  realMode = false;

  // tslint:disable-next-line: no-use-before-declare
  listData = listDataConst;

  isShow: boolean;
  topPosToStartShowing = 100;

  ngOnInit() {
  }

  openFilter() {
    if (this.filter === true) {
      this.filter = false;
    } else {
      this.filter = true;
    }
  }

  changeMode() {
    if (this.realMode === true) {
      this.realMode = false;
    } else {
      this.realMode = true;
    }
  }

  search(event: any) {

    this.changeLevel(this.selectedLevel);

    if (event.target.value !== '') {
      this.listData = this.listData.filter(item =>
        item.contentResearch.toLowerCase().indexOf(this.replaceKey(event.target.value).toLowerCase()) > -1
      );
    }

  }

  plusMeaningUse(listString: any[]) {
    let result = '';
    listString.forEach(element => {
      result += element.value + ' ';
    });
    return result;
  }

  plusSentence(listString: any[]) {
    let result = '';
    listString.forEach(element => {
      result += element.reibunJA + ' ' + element.reibunVN + ' ' + element.reibunRO;
    });
    return result;
  }

  replaceKey(key: string) {
    let i;
    let spaceCount = (key.split(' ').length);
    if (spaceCount <= 15) {
      spaceCount = 15;
    }
    for (i = 0; i <= spaceCount; i++) {
      key = key.replace('\n', '')
        .replace('…', '')
        .replace('\r', '')
        .replace('『', '')
        .replace('』', '')
        .replace('.', '')
        .replace('✙', '')
        .replace('　✙　', '')
        .replace('。', '')
        .replace(' ', '')
        .replace('/', '')
        .replace('(', '')
        .replace(')', '')
        .replace('　', '')
        .replace('“', '')
        .replace('”', '')
        .replace('～', '')
        .replace('~', '')
        .replace(' (', '')
        .replace('？', '')
        .replace('?', '')
        .replace('!', '')
        .replace('+', '')
        .replace('-', '')
        .replace('"', '');
    }
    return key;
  }

  changeLevel(event: any) {
    this.listData = listDataConst;
    this.selectedLevel = event;
    if (event !== 'All') {
      this.listData = this.listData.filter(item => item.level === event);
    }
  }

  @HostListener('window:scroll')
  checkScroll() {
    const scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;

    if (scrollPosition >= this.topPosToStartShowing) {
      this.isShow = true;
    } else {
      this.isShow = false;
    }
  }

  gotoTop() {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    });
  }

}

const listDataConst = data;
